/*--------------------------------

Icon Web Font
Generated using nucleoapp.com

-------------------------------- */
@font-face {
  font-family: 'Icon';
  font-weight: normal;
  font-style: normal;
}
/*------------------------
	base class definition
-------------------------*/
.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'Icon';
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  /* Better Font Rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*------------------------
  change icon size
-------------------------*/
/* relative units */
.icon-sm {
  font-size: 0.8em;
}
.icon-lg {
  font-size: 1.2em;
}
/* absolute units */
.icon-16 {
  font-size: 16px;
}
.icon-32 {
  font-size: 32px;
}

/*------------------------
  spinning icons
-------------------------*/
.icon-is-spinning {
  animation: icon-spin 1s infinite linear;
}
@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*------------------------
  rotated/flipped icons
-------------------------*/
.icon-rotate-90 {
  transform: rotate(90deg);
}
.icon-rotate-180 {
  transform: rotate(180deg);
}
.icon-rotate-270 {
  transform: rotate(270deg);
}
.icon-flip-y {
  transform: scaleY(-1);
}
.icon-flip-x {
  transform: scaleX(-1);
}
/*------------------------
	icons
-------------------------*/

.icon-logo-fb-simple::before {
  content: "\ea05";
}

.icon-logo-instagram::before {
  content: "\ea06";
}

.icon-logo-twitter::before {
  content: "\ea07";
}


* {
	font-size: 10px;
	font-family: 'Open-Sans', serif;
	color: $white;
}

h1, .heading--h1 {
	font-size: 40px;
	font-weight: 700;
}

p {
	font-size: 18px;
}


button {
	font-size: 18px;
	box-shadow: 2px 4px 9px 3px rgba(0, 0, 0, 0.258633);
}
* {
	box-sizing: border-box;
}

*::-webkit-scrollbar {
	display: none;
	-webkit-appearance: none;
}

*::selection {
	background-color: rgba(255, 255, 255, 0.99);
	color: $purple;
	opacity: 0.99;
}

body {
	position: relative;
	background-color: $purple;
	width: 100%;
	height: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	max-width: 100vw;
	overflow-x: hidden;
	overflow-y: scroll;
}

.section {
	width: 100%;
	max-width: 1440px;
	margin: 0 auto;

	&__inner {
		margin: 0 80px; 
	}
	
}

@media (max-width: 750px) {

	.section {

		&__inner {
			margin: 0 32px !important;
		}

	}

}
.background {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100vh !important;
	width: 100vw !important;
	max-width: 100vw;
	z-index: -50;
	overflow: hidden !important;

	&__triangle, .triangle {
		position: relative;
		width: 100%;
		height: 100%;
		margin: 0;

		&__item {
			content: "";
			position: absolute;			
			top: 50%;
			left: -32px;
			height: 120px;
			width: 150vh;
			min-width: 150vw;
			transform-origin: left;
			background: linear-gradient(270deg, rgba(249, 82, 197, 0.75) 0%, rgba(153, 82, 255, 0.0) 0%);
			
			&--top {
				transform:perspective(1500px) rotateY(-40deg) rotate(25deg)
			}
			
			&--bottom {
				transform:perspective(1500px) rotateY(-40deg) rotate(-25deg)
			}
			
		}

	}

	&__square {
		position: absolute;
		width: 100%;
		height: 100%;
		margin: 0;
		width: 32px;
		height: 32px;
		background: linear-gradient(135deg, rgba(249, 82, 197, 0.75) 0%, rgba(153, 82, 255, 0.0) 100%);
		transform: rotate(45deg);
		border-radius: 3px;

		&--one { top: 220px; left: 740px; width: 32px; height: 32px }
		&--two { top: 120px; left: 940px; width: 28px; height: 28px }
		&--three { top: 160px; left: 1240px; width: 40px; height: 40px; }
		&--four { top: 820px; left: 1140px; width: 32px; height: 32px}
		&--five { top: 720px; left: 1340px; width: 16px; height: 16px}

	}

	@media (max-width: 1050px) {
		height: 150vh !important;
		width: 150vw !important;

		&__triangle {	
	
			&:after, 
			&:before {
				top: 30%;
				left: -160px;
				height: 120px;
				width: 150vh;
				min-width: 150vw;
				transform-origin: left;
				background: linear-gradient(270deg, rgba(249, 82, 197, 0.75) 0%, rgba(153, 82, 255, 0.0) 80%);
			}

	}

	&__square {
		position: absolute;
		width: 100%;
		height: 100%;
		margin: 0;
		width: 32px;
		height: 32px;
		background: linear-gradient(135deg, rgba(249, 82, 197, 0.75) 0%, rgba(153, 82, 255, 0.0) 100%);
		transform: rotate(45deg);
		border-radius: 3px;

		&--one { top: 200px; left: 650px; width: 32px; height: 32px }
		&--two { top: 770px; left: 830px; width: 16px; height: 16px }
		&--three { top: 1000px; left: 120px; width: 40px; height: 40px; }
		&--four { top: 1400px; left: 320px; width: 16px; height: 16px}
		&--five { top: 1300px; left: 660px; width: 24px; height: 24px}

	}

}

}
.footer {
	padding: 64px 0;

	&__icons, .icons {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		gap: 16px;

		&__item {
			width: 40px;
			height: 40px;
			cursor: pointer;
			border: 1px solid $white;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 16px;
			transition: all 0.1s ease-in-out;

			&:hover {
				color: $magenta;
				border-color: $magenta;
			}

		}

	}

}
.header {
	padding: 64px 0;

	&__logo {
		height: 32px;
	}
	
}
.main {

	&__inner {
		display: flex;
		flex-direction: row;
		gap: 64px;
		justify-content: space-around;
	}

	&__preview, .preview {
		position: relative;
		width: 50%;
		max-width: 600px;
		margin: 0 auto;
		height: 480px;

		&__item, .item {
			border-radius: 16px;
			overflow: hidden;

			&__inner {
				position: relative;
				height: 100%;
			}

			&__navigation, .navigation {
				background-color: $black;
				height: 48px;
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				align-items: center;
				padding: 0 40px;
				border-radius: 4px;

				&__input {
					background-color: $grey-medium;
					height: 16px;
					width: 140px;
				}

				&__hamburger, .hamburger {
					width: 24px; 
					height: 16px;
					display: flex;
					flex-direction: column;
					justify-content: space-between;

					&__item {
						width: 100%;
						height: 3px;
						background-color: $grey-medium;
					}

				}

			}

			&__content, .content {
				padding: 64px;
				
				&__item, .item {
					opacity: 0;
					height: auto;
					width: 100%;
					display: flex;
					flex-direction: row;
					align-items: flex-start;
					gap: 32px;
					margin-bottom: 24px;

					&:nth-child(2n) .item__image { background: $gradient-cyan-blue; }
					&:nth-child(3n) .item__image { background: $gradient-purble-magenta; }
					

					&__image {
						width: 64px;
						min-width: 64px;
						height: 64px;
						display: block;
						background: $gradient-yellow-magenta;
						border-radius: 50%;
						transition: all 0.1s ease;
						cursor: pointer;
						
						&:hover {
							filter: brightness(0.85);
							overflow: visible;
						}
					}

					&__heading {
						height: 12px;
						width: 80px;
						margin-bottom: 4px;
						background-color: $grey-light;
					}

					&__text-wrapper {
						width: 100%;
					}

					&__paragraph {
						height: 3px;
						width: 100%;
						display: block;
						margin-bottom: 4px;
						background-color: $grey-light;
						
						&:nth-child(1) {
							height: 4px;
							margin-bottom: 4px !important; 
							width: 80px;
						}

						&:nth-child(2) {
						}

						&:nth-child(3) {
							width: calc(100% - 16px);
						}

						&:nth-child(4) {
							width: calc(100% - 32px);
						}


					}

				}

			}			


			&--big {
				position: absolute;
				top: 0;
				right: 40px;
				bottom: 80px;
				left: 0;
				background-color: $purple-light;
			}

			&--small {
				position: absolute;
				top: 140px;
				right: 0;
				bottom: 0;
				width: 220px;
				background-color: $black;
				background-color: $white;

				& .navigation {
					padding: 0 20px !important;
				}

				& .navigation__input {
					width: 72px !important;
				}

				& .content {
					padding: 40px 24px 24px 24px !important;


					&__item, .item {
						gap: 16px !important;
						margin-bottom: 8px !important;

						&__image {
							width: 32px !important;
							min-width: 32px !important;
							height: 32px !important;
						}

						&__heading {
							height: 6px !important;
							margin-bottom: 2px !important;
						}

						&__paragraph {
							margin-bottom: 2px !important;
						}

					}

				}

			}


			
		}

	}

	&__teaser, .teaser {
		width: 50%;
		max-width: 500px;
		display: flex;
		flex-direction: column;
		justify-content: center;

		&__title {
			margin-bottom: 24px
		}

		&__text {
			margin-bottom: 24px;
		}

		&__cta {
			background-color: $white;
			border-radius: 32px;	
			width: 100%;
			max-width: 200px;
			height: 56px;
			color: $purple;
			transition: all 0.1s ease-in-out;
			border: 0;
			outline: none;
			cursor: pointer;

			&:hover {
				background-color: $pink;
				color: $white;
			}

		}

	}

	@media (max-width: 1050px) {

		&__inner {
			flex-direction: column;
		}

		&__preview, .preview {
			width: 100%;
			height: 480px;
			margin: 80px auto 0 auto;
		}
		
		&__teaser, .teaser {
			width: 100%;
			height: 480px;
			margin: 0 auto;

			&__title, 
			&__text {
				text-align: center;
			}

			&__cta {
				margin: 0 auto;


			}

		}

	}

	@media (max-width: 750px) {

		&__preview .item__content {
				padding: 48px 32px 32px 32px !important;
		}


	}

}